let code = {}

code.overview = {}

code.overview.zh = `
\`radio\` 
`

code.base = {}

code.base.zh = `
*  v-model 
\`\`\`javascript
<template>
  <div class="input-warp">
    <qc-radio v-model="model1" label="apple"></qc-radio>
    <p>{{model1}}</p>
    <qc-radio v-model="model2" label="apple">Apple</qc-radio>
    <p>{{model2}}</p>
  </div>
</template>
<script>
  export default {
    date () {
      return {
        model1: false,
        model2: false
      }
    }
  }
</script>
\`\`\`
`
code.group = {}
code.group.zh = `
* \`qc-radio-group\`\`qc-radio\`  \`label\`  \`qc-radio\`  \`label\` 
\`\`\`javascript
<template>
  <div class="input-warp">
    <qc-radio-group v-model="group" >
      <qc-radio label="apple"></qc-radio>
      <qc-radio label="apple1"></qc-radio>
      <qc-radio label="apple2"></qc-radio>
    </qc-radio-group>
    <p>{{group}}</p>
    <div style="height: 10px;width:100%;"></div>
    <qc-radio-group v-model="group2" >
      <qc-radio label="apple">Apple</qc-radio>
      <qc-radio label="apple1">Apple1</qc-radio>
      <qc-radio label="apple2">Apple2</qc-radio>
    </qc-radio-group>
    <p>{{group2}}</p>
  </div>
</template>
<script>
  export default {
    date () {
      return {
        group: '',
        group2: 'apple'
      }
    }
  }
</script>
\`\`\`
`

code.disabled = {}
code.disabled.zh = `
* \`disabled\`
\`\`\`javascript
<template>
  <div class="input-warp">
    <div>
      <qc-radio disabled label="apple" v-model="model3"></qc-radio>
    </div>
    <div style="height: 10px;width:100%;"></div>
    <qc-radio-group v-model="group3" >
      <qc-radio disabled label="apple"></qc-radio>
      <qc-radio label="apple1"></qc-radio>
      <qc-radio label="apple2"></qc-radio>
    </qc-radio-group>
  </div>
</template>
<script>
  export default {
    date () {
      return {
        group3: '',
        model3: true
      }
    }
  }
</script>
\`\`\`
`

code.vertical = {}
code.vertical.zh = `
* \`vertical\`
\`\`\`javascript
<template>
  <div class="input-warp">
    <qc-radio-group v-model="group4" vertical>
      <qc-radio label="apple"></qc-radio>
      <qc-radio label="apple1"></qc-radio>
      <qc-radio label="apple2"></qc-radio>
    </qc-radio-group>
    <p>{{group4}}</p>
  </div>
</template>
<script>
  export default {
    date () {
      return {
        group4: ''
      }
    }
  }
</script>
\`\`\`
`

code.config = {}
code.config.zh = `
### qc-radioProps

| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| value      | false     |    Boolean   |   v-model  |
| label   |    -        |    [String, Number]   |   value  |
| disabled      |     ,false       |    Boolean    |   |
| change      |     event(@change="fn")      |    -    |   |
  
### qc-radio-groupProps

| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| value      |   -     |    [String, Number]   |    v-model  |
| vertical   |   false       |    Boolean   |   |
| change      |     event(@change="fn")      |    -    |  |
`
export default code



