<i18n>
  {
    "zh": {
      "eg_base_block": "",
      "eg_base_clear": "",
      "eg_base_group": "",
      "eg_base_multiple": "",
      "eg_base_config": ""
    }
  }
</i18n>
<template>
    <div>
      <div class="components-view">
      <j-code :overview="true" :langSources="code.overview"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_block') }}</div>
        <div class="input-warp">
          <qc-step-group :space="200" :active="1" finish-status='finish'>
            <qc-step title="1"></qc-step>
            <qc-step title="2"></qc-step>
            <qc-step title="3"></qc-step>
          </qc-step-group>
        </div>
        <j-code :langSources="code.base"></j-code><br>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_clear') }}</div>
        <div class="input-warp">
          <qc-step-group :space="200" :active="1" finish-status='finish'>
            <qc-step title="1">
              <div slot="description">1</div>
            </qc-step>
            <qc-step title="2">
              <div slot="description">2</div>
            </qc-step>
            <qc-step title="3">
              <div slot="description">3</div>
            </qc-step>
          </qc-step-group>
        </div>
        <j-code :langSources="code.close"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_group') }}</div>
        <div class="input-warp">
           <qc-step-group align-center :active="1" finish-status='finish'>
            <qc-step title="1">
              <div slot="description">1</div>
            </qc-step>
            <qc-step title="2">
              <div slot="description">2</div>
            </qc-step>
              
            <qc-step title="3">
              <div slot="description">3</div>
            </qc-step>
          </qc-step-group>
        </div>
        <j-code :langSources="code.button"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_multiple') }}</div>
        <div class="input-warp">
           <qc-step-group :space="200" direction="vertical" :active="1" finish-status='finish'>
            <qc-step title="1">
              <div slot="description">1</div>
            </qc-step>
            <qc-step title="2">
            <div slot="description">2</div>
            </qc-step>
              
            <qc-step title="3">
              <div slot="description">3</div>
            </qc-step>
          </qc-step-group>
        </div>
        <j-code :langSources="code.event"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_config') }}</div>
        <j-code :overview="true" :langSources="code.config"></j-code>
      </div>
    </div>
</template>

<script>
import code from './index.js'
export default {
  data () {
    return {
      code: code,
      value1: '',
      value2: '',
      message: ['', '', '', '', ''],
      model1: false,
      model2: false,
      model3: false,
      model4: false,
      model6: false,
      model7: '',
      model8: '',
      model9: ''
    }
  },
  methods: {
    myCancelCallback () {
      alert('')
    },
    myConfirmCallback () {
      alert('')
    },
    myCloseModal () {
      alert('')
    },
    clickMe () {
      this.$QC.modal.show({
        width: 600,
        title: 'js',
        content: 'js',
        closed: true,
        isMarkClose: true,
        confirm: () => {
          alert('js')
        }
      })
    }
  }
}
</script>
<style lang="scss">
  .components-view{
    margin: 10px;
  }
  .input-warp{
    width: 250px;
    margin-top:20px;
  }
  .click_me{
    width:40px;
    height:30px;
    background: #37ac68;
    border:none;
  }
</style>



