code.base = {};

code.base.zh = "\n* `qc-slide`\u9700\u8981\u4F20\u5165`list`\u5C5E\u6027\uFF0C\u8BE5\u5C5E\u6027\u662F\u4E00\u5C4F\u4E2D\u5171\u6709\u591A\u5C11\u5757\u5143\u7D20\uFF0C\u4E0D\u4F20\u4F1A\u629B\u51FA\u9519\u8BEF\n```javascript\n<template>\n  <div class=\"box\">\n    <qc-slide\n      :list=\"3\"\n    >\n      <qc-slide-item v-for=\"(item, key) in 5\" :key=\"key\">\n        <p class=\"content\" :style=\"{background: key%2===0?'#ccc':'#f8f8ff'}\">\u8FD9\u662F\u7B2C{{item}}\u6761\u4FE1\u606F</p>\n      </qc-slide-item>\n    </qc-slide>\n  </div>\n</template>\n<style lang=\"scss\">\n  .box{\n    width: 400px;\n    height:120px;\n    margin:auto;\n  }\n  .qc-slide_carousel{\n    width: 100%;\n  }\n  .content{\n    padding:0;\n    margin:0;\n    height: 40px;\n    line-height: 40px;\n  }\n</style>\n```\n";

code.direction = {};
code.direction.zh = "\n* \u53EF\u901A\u8FC7\u8BBE\u7F6E`direction`\u8BBE\u7F6E\u5207\u6362\u65B9\u5411\uFF0C\u9ED8\u8BA4\u662F`vertical`\n```javascript\n<template>\n  <div class=\"container\">\n    <qc-slide\n      :list=\"3\"\n      direction=\"horizontal\"\n    >\n      <qc-slide-item v-for=\"(item, key) in 5\" :key=\"key\" class=\"container-item\">\n        <p class=\"container-content\" :style=\"{background: key%2===0?'#ccc':'#f8f8ff'}\">\u8FD9\u662F\u7B2C{{item}}\u6761\u4FE1\u606F</p>\n      </qc-slide-item>\n    </qc-slide>\n  </div>\n</template>\n<style lang=\"scss\">\n  .container{\n    width: 900px;\n    height: 150px;\n    margin: auto;\n    .container-item{\n      width: 300px;\n      display: inline-block;\n      height: 150px;\n      text-align:center;\n      .container-content{\n        line-height:150px;\n      }\n    }\n  }\n</style>\n```\n";
code.interval = {};
code.interval.zh = "\n* \u53EF\u901A\u8FC7\u8BBE\u7F6E`interval`\u8BBE\u7F6E\u5207\u6362\u5EF6\u65F6\uFF0C\u9ED8\u8BA4\u662F`3000`\n```javascript\n<template>\n  <div class=\"box\">\n    <qc-slide\n      :list=\"3\"\n      :interval=\"4000\"\n    >\n      <qc-slide-item v-for=\"(item, key) in 5\" :key=\"key\">\n        <p class=\"content\" :style=\"{background: key%2===0?'#ccc':'#f8f8ff'}\">\u8FD9\u662F\u7B2C{{item}}\u6761\u4FE1\u606F</p>\n      </qc-slide-item>\n    </qc-slide>\n  </div>\n</template>\n<style lang=\"scss\">\n  .box{\n    width: 400px;\n    height:120px;\n    margin:auto;\n  }\n  .qc-slide_carousel{\n    width: 100%;\n  }\n  .content{\n    padding:0;\n    margin:0;\n    height: 40px;\n    line-height: 40px;\n  }\n</style>\n```\n";

code.auto = {};
code.auto.zh = "\n* \u53EF\u901A\u8FC7\u8BBE\u7F6E`is-auto`\u8BBE\u7F6E\u662F\u5426\u81EA\u52A8\u5207\u6362\uFF0C\u9ED8\u8BA4\u662F`false`\n```javascript\n<template>\n <div class=\"box\">\n    <qc-slide\n      :list=\"3\"\n      :is-auto=\"false\"\n    >\n      <qc-slide-item v-for=\"(item, key) in 5\" :key=\"key\">\n        <p class=\"content\" :style=\"{background: key%2===0?'#ccc':'#f8f8ff'}\">\u8FD9\u662F\u7B2C{{item}}\u6761\u4FE1\u606F</p>\n      </qc-slide-item>\n    </qc-slide>\n  </div>\n</template>\n<style lang=\"scss\">\n  .box{\n    width: 400px;\n    height:120px;\n    margin:auto;\n  }\n  .qc-slide_carousel{\n    width: 100%;\n  }\n  .content{\n    padding:0;\n    margin:0;\n    height: 40px;\n    line-height: 40px;\n  }\n</style>\n```\n";
code.duration = {};
code.duration.zh = "\n* \u53EF\u901A\u8FC7\u8BBE\u7F6E`is-auto`\u8BBE\u7F6E\u662F\u5426\u81EA\u52A8\u5207\u6362\uFF0C\u9ED8\u8BA4\u662F`false`\n```javascript\n<template>\n  <div class=\"container\">\n    <qc-slide\n      :list=\"3\"\n      :duration=\"400\"\n      direction=\"horizontal\"\n    >\n      <qc-slide-item v-for=\"(item, key) in 5\" :key=\"key\" class=\"container-item\">\n        <p class=\"container-content\" :style=\"{background: key%2===0?'#ccc':'#f8f8ff'}\">\u8FD9\u662F\u7B2C{{item}}\u6761\u4FE1\u606F</p>\n      </qc-slide-item>\n    </qc-slide>\n  </div>\n</template>\n<style lang=\"scss\">\n  .container{\n    width: 900px;\n    height: 150px;\n    margin: auto;\n    .container-item{\n      width: 300px;\n      display: inline-block;\n      height: 150px;\n      text-align:center;\n      .container-content{\n        line-height:150px;\n      }\n    }\n  }\n</style>\n```\n";

code.indicator = {};
code.indicator.zh = "\n* \u5177\u4F53\u6839\u636E\u4E1A\u52A1\u573A\u666F\u5B9A\n```javascript\n<template>\n  <div class=\"container\">\n    <qc-slide\n      :list=\"3\"\n      direction=\"horizontal\"\n      ref=\"slide\"\n    >\n      <qc-slide-item v-for=\"(item, key) in 5\" :key=\"key\" class=\"container-item\">\n        <p class=\"container-content\" :style=\"{background: key%2===0?'#ccc':'#f8f8ff'}\">\u8FD9\u662F\u7B2C{{item}}\u6761\u4FE1\u606F</p>\n      </qc-slide-item>\n    </qc-slide>\n  </div>\n  <div class=\"indicator\">\n    <button \n      @click=\"$refs.slide.handelPrev()\"\n      @mouseenter.stop=\"$refs.slide.stop()\" \n      @mouseleave.stop=\"$refs.slide.autoPlay()\"\n    >prev</button>\n    <button \n      @click=\"$refs.slide.handelNext()\"\n      @mouseenter.stop=\"$refs.slide.stop()\" \n      @mouseleave.stop=\"$refs.slide.autoPlay()\"\n    >next</button>\n  </div>\n</template>\n<style lang=\"scss\">\n  .container{\n    width: 900px;\n    height: 150px;\n    margin: auto;\n    .container-item{\n      width: 300px;\n      display: inline-block;\n      height: 150px;\n      text-align:center;\n      .container-content{\n        line-height:150px;\n      }\n    }\n  }\n  .indicator{\n    margin-top: 20px;\n    text-align:center;\n  }\n</style>\n```\n";

