code.interval = {};
code.interval.zh = "\n* \u53EF\u901A\u8FC7`interval`\u5C5E\u6027\u8BBE\u7F6E\u8F6E\u64AD\u56FE\u8F6E\u64AD\u5EF6\u65F6\uFF0C\u5F53`interval`\u8BBE\u7F6E\u4E3A`0`\u65F6\uFF0C\u8F6E\u64AD\u56FE\u5173\u95ED\u81EA\u52A8\u8F6E\u64AD\uFF0C\u9ED8\u8BA4\u8BBE\u7F6E`3000`\n```javascript\n<template>\n  <div class=\"container\">\n    <div class=\"inner\">\n      <qc-swiper\n        :height=\"300\"\n        :interval=\"5000\"\n      >\n        <qc-swiper-item v-for=\"(item,index) in 4\" :key=\"item\" :style=\"{background: index%2===0?'#99a9bf':'#d3dce6'}\">\n          <p>\u8FD9\u662F\u7B2C{{item}}\u5F20</p>\n        </qc-swiper-item>\n      </qc-swiper>\n    </div>\n    <div class=\"inner\">\n      <qc-swiper\n        :height=\"300\"\n        :interval=\"0\"\n      >\n        <qc-swiper-item v-for=\"(item,index) in 4\" :key=\"item\" :style=\"{background: index%2===0?'#99a9bf':'#d3dce6'}\">\n          <p>\u8FD9\u662F\u7B2C{{item}}\u5F20</p>\n        </qc-swiper-item>\n      </qc-swiper>\n    </div>\n  </div>\n</template>\n```\n";
