code.value = {};
code.value.zh = "\n* \u53EF\u901A\u8FC7\u53CC\u5411\u7ED1\u5B9A\u503C\u6539\u53D8\u7B2C\u4E00\u6B21\u8F6E\u64AD\u5C55\u793A\u7684\u56FE\u7247,\u540C\u65F6`qc-swiper`\u6BCF\u6B21\u5C55\u793A\u7684\u56FE\u7247\u7684`index`\uFF0C\u53EF\u4EE5\u901A\u8FC7\u76D1\u542C\u8FD9\u4E2A\u53CC\u5411\u7ED1\u5B9A\u503C\u83B7\u5F97\n```javascript\n<template>\n  <div class=\"container\">\n    <div class=\"inner\">\n      <qc-swiper\n        :height=\"300\"\n      >\n        <qc-swiper-item v-for=\"(item,index) in 4\" :key=\"item\" :style=\"{background: index%2===0?'#99a9bf':'#d3dce6'}\">\n          <p>\u8FD9\u662F\u7B2C{{item}}\u5F20</p>\n        </qc-swiper-item>\n      </qc-swiper>\n    </div>\n    <div class=\"inner\">\n      <qc-swiper\n        :height=\"300\"\n        v-model=\"pageNum\"\n      >\n        <qc-swiper-item v-for=\"(item,index) in 4\" :key=\"item\" :style=\"{background: index%2===0?'#99a9bf':'#d3dce6'}\">\n          <p>\u8FD9\u662F\u7B2C{{item}}\u5F20</p>\n        </qc-swiper-item>\n      </qc-swiper>\n    </div>\n  </div>\n</template>\n<script>\n  export default {\n    data () {\n      return {\n        pageNum: 2\n      }\n    }\n  }\n</script>\n```\n";
