code.fade.zh = "\n* \u53EF\u901A\u8FC7\u8BBE\u7F6E`fade`\u7684\u503C\u4E3A`true`\u8BBE\u7F6E\u8F6E\u64AD\u56FE\u8F6E\u64AD\u7684\u6548\u679C\u4E3A\u6E10\u9690\u6E10\u73B0\uFF0C\u9ED8\u8BA4\u4E3A`false`\n```javascript\n<template>\n  <div class=\"container\">\n    <div class=\"inner\">\n      <qc-swiper\n        :height=\"300\"\n      >\n        <qc-swiper-item v-for=\"(item,index) in 4\" :key=\"item\" :style=\"{background: index%2===0?'#99a9bf':'#d3dce6'}\">\n          <p>\u8FD9\u662F\u7B2C{{item}}\u5F20</p>\n        </qc-swiper-item>\n      </qc-swiper>\n    </div>\n    <div class=\"inner\">\n      <qc-swiper\n        :height=\"300\"\n        :fade=\"true\"\n      >\n        <qc-swiper-item v-for=\"(item,index) in 4\" :key=\"item\" :style=\"{background: index%2===0?'#99a9bf':'#d3dce6'}\">\n          <p>\u8FD9\u662F\u7B2C{{item}}\u5F20</p>\n        </qc-swiper-item>\n      </qc-swiper>\n    </div>\n  </div>\n</template>\n```\n";
