<i18n>
  {
    "zh": {
      "eg_base_block": "",
      "eg_base_clear": "",
      "eg_base_group": "",
      "eg_base_multiple": "",
      "eg_base_filterable": "",
      "eg_base_config": ""
    }
  }
</i18n>
<template>
    <div>
      <div class="components-view">
      <j-code :overview="true" :langSources="code.overview"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_block') }}</div>
        <div class="input-warp">
          <button class="click_me" @click="model1=true"></button>
          <qc-modal v-model="model1" title="" content="" :width="600"></qc-modal>
          <p>{{model1}}</p>
        </div>
        <j-code :langSources="code.base"></j-code><br>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_clear') }}</div>
        <div class="input-warp">
          <button class="click_me" @click="model2=true"></button>
          <qc-modal :is-mark-close="false" :closed="true" v-model="model2" title="2" content="2" :width="600"></qc-modal>
          <p>{{model2}}</p>
        </div>
        <j-code :langSources="code.close"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_group') }}</div>
        <div class="input-warp">
          <button class="click_me" @click="model3=true"></button>
          <qc-modal :closed="true" v-model="model3" title="3" content="3" :width="600" confirm-text="confirm" cancel-text="cancel"></qc-modal>
          <p>{{model3}}</p>
        </div>
        <j-code :langSources="code.button"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_multiple') }}</div>
        <div class="input-warp">
          <button class="click_me" @click="model4=true"></button>
          <qc-modal :is-mark-close="true" :closed="true" v-model="model4" title="4" content="4" :width="600" @cancel-callback="myCancelCallback" @confirm-callback="myConfirmCallback" @close-modal="myCloseModal"></qc-modal>
          <p>{{model4}}</p>
        </div>
        <j-code :langSources="code.event"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_filterable') }}</div>
        <div class="input-warp">
          <button class="click_me" @click="model6=true"></button>
          <qc-modal :is-mark-close="true" :scrollable="true" :closed="true" v-model="model6" title="5" content="5" :width="600" ></qc-modal>
          <p>{{model6}}</p>
        </div>
        <j-code :langSources="code.scroll"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_filterable') }}</div>
        <div class="input-warp">
          <button class="click_me" @click="clickMe"></button>
        </div>
        <j-code :langSources="code.js"></j-code>
      </div>
      <div class="components-view">
        <div class="view-title">{{ $t('eg_base_config') }}</div>
        <j-code :overview="true" :langSources="code.config"></j-code>
      </div>
    </div>
</template>

<script>
import code from './index.js'
export default {
  data () {
    return {
      code: code,
      value1: '',
      value2: '',
      message: ['', '', '', '', ''],
      model1: false,
      model2: false,
      model3: false,
      model4: false,
      model6: false,
      model7: '',
      model8: '',
      model9: ''
    }
  },
  methods: {
    myCancelCallback () {
      alert('')
    },
    myConfirmCallback () {
      alert('')
    },
    myCloseModal () {
      alert('')
    },
    clickMe () {
      this.$QC.modal.show({
        width: 600,
        title: 'js',
        content: 'js',
        closed: true,
        isMarkClose: true,
        confirm: () => {
          alert('js')
        }
      })
    }
  }
}
</script>
<style lang="scss">
  .components-view{
    margin: 10px;
  }
  .input-warp{
    width: 250px;
    margin-top:20px;
  }
  .click_me{
    width:40px;
    height:30px;
    background: #37ac68;
    border:none;
  }
</style>



