code.base.zh = `
*  \`v-model\` 
* \`value\` \`qc-option\`value
* updownEnterEsc
\`\`\`javascript
<template>
  <div class="input-warp">
    <qc-select v-model="model1">
      <qc-option v-for="(item,index) in message" :value="item" :key="index"></qc-option>
    </qc-select>
    <p>{{model1}}</p>
  </div>
</template>
<script>
export default {
  data () {
    return {
      message: ['', '', '', '', ''],
      model1: ''
    }
  }
}
</script>
\`\`\`
`

