code.config = {}
code.config.zh = `
### qc-elect props

| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| value      |        |    [String, Number, Array]   |   value  v-model  String  Number Array |
| multiple   |    false       |    Boolean  |   |
| multipleLimit   |    (0)         |    Number   |   0  |
| disabled |     false      |    Boolean   |   |
| clearable      |      false      |    Boolean   |  |
| filterable      |      false       |    Boolean   |  |
| size      |      -       |    String   | largesmall |
| placeholder      |           |    String   |  |
| no-data-text      |             |    String   |  |
| no-match-text      |      |    string   |  |
| multiple-show      |      true      |    Boolean   | label |
| getLabel      |      @getLabel="fn"      |    -   | label |

### qc-option props
| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| value      |        |    [String, Number]   |   |
| label   |    -      |    String  |   slot slot  label  label  value label  |
| disabled   |    false         |    Boolean   |    |

### qc-option-group props
| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| title      |        |    String   |   |
`
export default code



