code.currentPage = {}
code.currentPage.zh = `
* input'v-qyValidator-error'inputemit'errormsg'
\`\`\`javascript
<template>
  <div>
    <my-input :validator-obj="validatorObj" @errormsg='msgPrint'></my-input>
    <div>{{printVal}}</div>
  </div>
</template>
<script>
import myInput from './myinput.vue'
export default {
  data () {
    return {
      validatorObj: {
        id: 'myInput',
        format: 'Mobile',
        title: '',
        request: ''
      },
      printVal: ''
    }
  },
  components: {
    myInput
  },
  methods: {
    msgPrint (val) {
      this.printVal = val
    }
  }
}
</script>
\`\`\`
* myinput
\`\`\`javascript
<template>
  <div class="input">
    <input type="text" v-qyValidator-input:reg="validatorObj">
    <div v-qyValidator-error:id="{id:id}"></div>
  </div>
</template>
<script type="text/javascript">
  export default {
    props: {
      validatorObj: {
        type: Object,
        default: {}
      }
    },
    data () {
      return {
        id: this.validatorObj.id
      }
    }
  }
</script>
\`\`\`
`

