code.event = {}
code.event.zh = `
* \`confirm-callback\`
* \`cancel-callback\` 
* \`close-modal\` 
\`\`\`javascript
<template>
  <div class="input-warp">
    <button class="click_me" @click="model4=true"></button>
    <qc-modal :is-mark-close="true" :closed="true" v-model="model4" title="4" content="4" :width="600" 
    @cancel-callback="myCancelCallback" @confirm-callback="myConfirmCallback" @close-modal="myCloseModal"></qc-modal>
    <p>{{model4}}</p>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        model4: false
      }
    },
    methods: {
      myCancelCallback () {
        alert('')
      },
      myConfirmCallback () {
        alert('')
      },
      myCloseModal () {
        alert('')
      }
    }
  }
</script>
\`\`\`
`
