code.js = {}
code.js.zh = `
* htmldomJavaScript\` this.$QC.modal.show\`jsmodalprops
\`\`\`javascript
<template>
  <div class="input-warp">
    <button class="click_me" @click="clickMe"></button>
  </div>
</template>
<script>
  export default {
    methods: {
      clickMe () {
        this.$QC.modal.show({
          width: 600,
          title: 'js',
          content: 'js',
          closed: true,
          isMarkClose: true,
          confirm: () => {
            alert('js')
          }
        })
      }
    }
  }
</script>
\`\`\`
`

code.config = {}
code.config.zh = `
### qc-modal props

| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| value      |   false     |    Boolean   | true  |
| width   |    520       |    [Number, String]  |   |
| title   |    ''         |    String   |   |
| closed |     false      |    Boolean   |   |
| is-mark-close      |      false      |    Boolean   |  |
| confirm-text      |      ''      |    String   |  |
| cancel-text      |      ''       |    String   |  |
| confirm-button      |     {}      |    Object   | buttonprops |
| cancel-button      |      {}       |    Object   | buttonprops |
| scrollable     |    false  |    Boolean   |  |
| content      |      ''      |    String   | html |
| button-position      |      'right'      |    String   |  |
| confirm     |     (){}      |    Function   |  |
| cancel     |     (){}      |    Function   |  |
| show-cancel     |      true      |    Boolean   |  |
| show-confirm     |    true |    Boolean   |  |
| disabled-confirm      |      false      |    Boolean   |  |

### qc-modal emit
| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| close-modal      |   -    |    -   |   |
| cancel-callback     |   -    |    -   |   |
| confirm-callback    |   -    |    -   |   |
`
export default code



