let code = {}

code.overview = {}

code.overview.zh = `
\`qc-slide\` 
`

code.base = {}

code.base.zh = `
* \`qc-slide\`\`list\`
\`\`\`javascript
<template>
  <div class="box">
    <qc-slide
      :list="3"
    >
      <qc-slide-item v-for="(item, key) in 5" :key="key">
        <p class="content" :style="{background: key%2===0?'#ccc':'#f8f8ff'}">{{item}}</p>
      </qc-slide-item>
    </qc-slide>
  </div>
</template>
<style lang="scss">
  .box{
    width: 400px;
    height:120px;
    margin:auto;
  }
  .qc-slide_carousel{
    width: 100%;
  }
  .content{
    padding:0;
    margin:0;
    height: 40px;
    line-height: 40px;
  }
</style>
\`\`\`
`


code.direction = {}
code.direction.zh = `
* \`direction\`\`vertical\`
\`\`\`javascript
<template>
  <div class="container">
    <qc-slide
      :list="3"
      direction="horizontal"
    >
      <qc-slide-item v-for="(item, key) in 5" :key="key" class="container-item">
        <p class="container-content" :style="{background: key%2===0?'#ccc':'#f8f8ff'}">{{item}}</p>
      </qc-slide-item>
    </qc-slide>
  </div>
</template>
<style lang="scss">
  .container{
    width: 900px;
    height: 150px;
    margin: auto;
    .container-item{
      width: 300px;
      display: inline-block;
      height: 150px;
      text-align:center;
      .container-content{
        line-height:150px;
      }
    }
  }
</style>
\`\`\`
`
