code.indicator = {}
code.indicator.zh = `
* 
\`\`\`javascript
<template>
  <div class="container">
    <qc-slide
      :list="3"
      direction="horizontal"
      ref="slide"
    >
      <qc-slide-item v-for="(item, key) in 5" :key="key" class="container-item">
        <p class="container-content" :style="{background: key%2===0?'#ccc':'#f8f8ff'}">{{item}}</p>
      </qc-slide-item>
    </qc-slide>
  </div>
  <div class="indicator">
    <button 
      @click="$refs.slide.handelPrev()"
      @mouseenter.stop="$refs.slide.stop()" 
      @mouseleave.stop="$refs.slide.autoPlay()"
    >prev</button>
    <button 
      @click="$refs.slide.handelNext()"
      @mouseenter.stop="$refs.slide.stop()" 
      @mouseleave.stop="$refs.slide.autoPlay()"
    >next</button>
  </div>
</template>
<style lang="scss">
  .container{
    width: 900px;
    height: 150px;
    margin: auto;
    .container-item{
      width: 300px;
      display: inline-block;
      height: 150px;
      text-align:center;
      .container-content{
        line-height:150px;
      }
    }
  }
  .indicator{
    margin-top: 20px;
    text-align:center;
  }
</style>
\`\`\`
`

code.config = {}
code.config.zh = `
| name        | default     |   type      | description |
| ----------- |-------------|-------------|-------------|
| direction      | (vertical)     |    String   |   |
| interval   |    (3000)         |    Number   |   |
| list   |            |    Number   |   |
| isAuto |      (true)       |    Boolean   |   |
| duration      |      (300)       |    Number   | (1000) |
`
export default code



